<template>
  <v-app id="app">
    <step-baner />
    <router-view />
  </v-app>
</template>

<script>
import StepBaner from '@/components/StepBaner.vue';
import { mapGetters, mapActions, mapMutations } from 'vuex'
export default {
  components: {
    StepBaner
  },
  data: () => ({
    
  }),
  computed: {
    ...mapGetters('scenarios', ['id']),
  },
  mounted() {
    var idTopic = document.getElementById('topic').dataset['id'];
    if (idTopic) {
      this.setId(idTopic);
      this.getTopic(idTopic).then(() => {
        this.setup();
      })
    }
  },
  methods: {
    ...mapActions('scenarios', ['setup','getTopic']),
    ...mapMutations('scenarios', ['setId']),
  }
}
</script>

<style lang="scss">
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

#nav {

  a {
    color: #2c3e50;

    &.router-link-exact-active {
      color: #42b983;
    }
  }
}
</style>
