<template>
  <v-hover v-slot="{ hover }">
    <v-card
      :elevation="hover ? 10 : 0"
      :color="(scenario.id) ? scenarioColors[nr] : '#EBEBEB'"
      :to="'/scenariusze/uklad/' + nr"
      class="w-30-ns scenario-card rounded-lg relative"
      
    >
      <div class="card-wrap pa6 lh-solid">
        <div v-if="scenario.title" class="f4 fw7 tc c-dark">{{ scenario.title }}</div>
        <div v-else class="f3 fw7 tc c-light">Scenariusz {{ nr }}</div>
      </div>
    </v-card>
  </v-hover>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'ScenarioCard',
  props: ['id', 'nr'],
  data: () => ({
    scenarioColors: {
      '1': '#EF9A9A',
      '2': '#81D4FA',
      '3': '#A5D6A7',
      '4': '#FFCC80'
    }
  }),
  computed: {
    ...mapGetters('scenarios', ['scenarioByNr', 'loading']),
    scenario() {
      return this.scenarioByNr(this.nr)
    },
    
  },
}
</script>

<style>
.scenario-card {
  will-change: box-shadow;
  transition-duration: 0.28s;
  transition-property: box-shadow, transform, opacity;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.card-wrap {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  transform: translateY(-50%);
}
</style>