<template>
  <div class="w-100 overflow-hidden mt4 mb2">
      <v-carousel v-model="sliderModel" height="120" light :show-arrows="false" touchless hide-delimiters>
        <v-carousel-item
          v-for="(step, i) in steps"
          :key="i"
        >
          <section class="section-wrap center w-100 w-90-l ph5 flex relative items-center h-100">
            <svg class="bgbaner" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1325.535 119.851">
              <path id="Path_1120" data-name="Path 1120" d="M-13.363-2.91C29.014-13.254,1161.3.241,1229.271,11.339s29.594,81.383-10.636,86.007S29.8,119.4-27.1,110.978-55.74,7.435-13.363-2.91Z" transform="translate(59.054 6.608)" fill="#f6f6f6"/>
            </svg>
            <div class="pr4">
              <v-avatar
                slot="icon"
                :color="step.color"
                size="52"
              >
                <span class="white--text f3">{{ i + 1 }}</span>
              </v-avatar>
            </div>
            <div class="c-dark flex-grow-1">
              <div class="f3 fw7">{{ step.subtitle }}</div>
              <div>{{ step.content }}</div>
            </div>
            <div class="pr2">
              <v-btn
                icon
                color="primary"
                x-large
                height="64"
                width="64"
                title="Wróć do poprzedniego kroku"
                @click="prevSlide"
                :disabled="currentSlide == 0"
              >
                <v-icon>mdi-arrow-left</v-icon>
              </v-btn>
            </div>
            <div class="pt0">
              <v-hover v-slot="{ hover }">
                <v-btn
                  :elevation="hover ? 6 : 0"
                  rounded
                  :color="(steps[currentSlide + 1]) ? steps[currentSlide + 1].color : 'primary'"
                  depressed
                  class="xxxl white--text"
                  :disabled="possibleStep <= currentSlide"
                  @click="nextSlide()"
                >
                  Dalej
                </v-btn>
              </v-hover>
            </div>
          </section>
        </v-carousel-item>
      </v-carousel>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
export default {
  data: () => ({
    sliderModel: 0
  }),
  computed: {
    ...mapGetters('scenarios', ['steps','currentStep','currentSlide','possibleStep']),
  },
  watch: {
    currentSlide(slide) {
      if (slide != this.sliderModel) {
        this.sliderModel = slide
      }
    }
  },
  methods: {
    ...mapMutations('scenarios', ['setCurrentStep','setCurrentSlide']),
    prevSlide() {
      this.setCurrentSlide(this.currentSlide - 1)
    },
    nextSlide() {
      let nextSlide = this.currentSlide + 1;
      if (nextSlide > this.currentStep) {
        this.setCurrentStep(nextSlide);
      }
      this.setCurrentSlide(nextSlide);
    }
  }
}
</script>