import Vue from 'vue'
import VueRouter from 'vue-router'
import Diagram from '../views/Diagram.vue'
import Scenariusze from '../views/Scenariusze.vue'
import Scenariusz from '../views/Scenariusz.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/scenariusze',
    name: 'Diagram',
    component: Diagram
  },
  {
    path: '/scenariusze/uklad',
    name: 'Scenariusze',
    component: Scenariusze
  },
  {
    path: '/scenariusze/uklad/:nr',
    name: 'Scenariusz',
    component: Scenariusz,
    props: true
  }
]

const router = new VueRouter({
  mode: 'history',
  routes: routes
})

export default router
