<template>
  <div class="w-100 overflow-hidden">
    <section>
      <h1 class="tc c-dark fw9 mv5">{{ title }}</h1>
    </section>

    <section v-if="this.loading" class="pv3 tc">
      <v-progress-circular
        :size="566"
        :width="8"
        color="#C9C9C9"
        indeterminate
      ></v-progress-circular>
    </section>
    <section v-else class="diagram pb5">
      <div class="section-wrap center ph3 w-100 w-90-l">
        
        <!-- czynniki kluczowe / sily napedowe -->
        <div class="flex flex-wrap w-100">

          <div class="w-50">
            <div class="w-100 flex flex-wrap">
          
              <div class="w-third flex flex-column justify-center di-le">
                <div class="chip-wrap" v-for="(item, i) in keyFactors" :key="i" :class="[nodeClass(i, keyFactorsLength)]">
                  <v-chip
                    label
                    link
                    :color="steps[1].color"
                    text-color="white"
                    class="rounded-lg"
                  >{{ item.title }}</v-chip>
                </div>
              </div>
              <div class="w-2"><!--currentStep > 1--></div>
              <div class="w-third flex items-end">
                <div class="w-100 pv7 relative z-1" :class="{ 'di-step-2': keyFactorsLength > 0 }">
                  <!--select key factors-->
                  <v-dialog
                    v-model="dialogKey"
                    scrollable
                    persistent
                    max-width="600px"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-hover v-slot="{ hover }">
                        <v-btn block height="64" 
                          :elevation="hover ? 8 : 0" 
                          :outlined="(currentStep > 0) ? false : true"
                          :color="steps[1].color"
                          :class="{'white--text': currentStep > 0 }"
                          :disabled="currentStep < 1"
                          v-bind="attrs"
                          v-on="on"
                          class="rounded-lg"
                        >
                          {{ steps[1].title }}
                        </v-btn>
                      </v-hover>
                    </template>
                    <v-card>
                      <v-card-title>Wybierz 2 czynniki kluczowe</v-card-title>
                      <v-divider></v-divider>
                      
                      <v-card-text class="mt3">
                        <v-chip-group
                          v-model="keyFactorsSelection"
                          column
                          multiple
                          active-class="chip2"
                        >
                          <v-chip
                            v-for="(item, i) in factors" 
                            :key="i" 
                            filter
                            label
                            class="rounded-lg"
                            :value="item.id"
                            :disabled="keyFactorsSelectionLength >= 2 && !keyFactorsSelection.includes(item.id)"
                          >
                            {{ item.title }}
                          </v-chip>
                        </v-chip-group>
                      </v-card-text>

                      <v-divider></v-divider>
                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn
                          text
                          @click="dialogKey = false"
                        >
                          Anuluj
                        </v-btn>
                        <v-btn
                          text
                          @click="saveKeyFactors"
                        >
                          Zapisz
                        </v-btn>
                      </v-card-actions>
                    </v-card>
                  </v-dialog>

                  <v-btn
                    fab
                    rounded
                    text
                    elevation="0"
                    class="indicator-2"
                  >2</v-btn>
                  <div class="triangle-down"></div>
                </div>
              </div>
              <div class="w-2"></div>

            </div>
          </div>

          <div class="w-50">
            <div class="w-100 flex flex-wrap">

              <div class="w-2"></div>
              <div class="w-third flex items-end">
                <div class="w-100 pv7 relative z-1" :class="{ 'di-step-3': forcesLength > 0 }">
                  <v-hover v-slot="{ hover }">
                    <v-btn block height="64" 
                      :elevation="hover ? 8 : 0"
                      :outlined="(currentStep > 1) ? false : true"
                      :color="steps[2].color"
                      :class="{'white--text': currentStep > 1 }"
                      :disabled="currentStep < 2"
                      @click.stop="addFactor(3)"
                      class="rounded-lg"
                    >
                      {{ steps[2].title }}
                    </v-btn>
                  </v-hover>
                  <v-btn
                    fab
                    rounded
                    text
                    elevation="0"
                    class="indicator-3"
                  >3</v-btn>
                  <div class="triangle-down"></div>
                </div>
              </div>
              <div class="w-2"></div>
              <div class="w-third flex flex-column justify-center di-r">
                <div class="chip-wrap"  v-for="(item, i) in forces" :key="i" :class="[nodeClass(i, forcesLength)]">
                  <v-chip
                    label
                    link
                    :color="steps[2].color"
                    text-color="white"
                    class="rounded-lg"
                  >{{ item.title }}</v-chip>
                </div>
              </div>

            </div>
          </div>

        </div>

        <!-- scenariusze -->
        <div class="flex flex-wrap w-100">
          <div class="w-2"></div>
          <div class="w-1"></div>
          <div class="w-2"></div>
          <div class="w-2 relative di-step-5">
            
            <v-hover v-slot="{ hover }">
              <v-btn block height="64"
                :elevation="hover ? 8 : 0" 
                :outlined="(currentStep > 2) ? false : true"
                :color="steps[4].color"
                :class="{'white--text': currentStep > 2 }"
                :disabled="currentStep < 3"
                :to="'/scenariusze/uklad'"
                class="rounded-lg z1"
              >
                Scenariusze
              </v-btn>
            </v-hover>
            <v-btn
              fab
              rounded
              text
              elevation="0"
              class="indicator-5"
            >5</v-btn>
          </div>
          <div class="w-2"></div>
          <div class="w-1"></div>
          <div class="w-2"></div>
        </div>

        <!-- czynniki / wildcards -->
        <div class="flex flex-wrap w-100">
          
          <div class="w-50">
            <div class="w-100 flex flex-wrap">
            
              <div class="w-third flex flex-column justify-center di-le relative z-1">
                <div class="chip-wrap" v-for="(item, i) in factors" :key="i" :class="[nodeClass(i, factorsLength)]">
                  <v-chip
                    label
                    link
                    :color="steps[0].color"
                    text-color="white"
                    @click.stop="editFactor(item)"
                    class="tc rounded-lg"
                  >{{ item.title }}</v-chip>
                </div>
              </div>

              <div class="w-2"></div>
              <div class="w-third flex items-start">
                <div class="w-100 pv7 relative" :class="{ 'di-step-1': factorsLength > 0 }">
                  <!--<div class="css-xuabex z-0"><div class="css-1kyirur"></div><div class="css-1kyirur"></div><div class="css-1kyirur"></div></div>-->
                  <v-hover v-slot="{ hover }">
                    <v-btn block height="64" 
                      :elevation="hover ? 8 : 0"
                      :color="steps[0].color"
                      @click.stop="addFactor(1)"
                      class="white--text rounded-lg">
                      {{ steps[0].title }}
                    </v-btn>
                  </v-hover>

                  <v-btn
                    fab
                    rounded
                    elevation="0"
                    class="indicator-1"
                  >1</v-btn>
                  <div class="triangle-up"></div>
                </div>
              </div>

              <div class="w-2"></div>

            </div>
          </div>


          <div class="w-50">
            <div class="w-100 flex flex-wrap">
              <div class="w-2"></div>

              <div class="w-third flex items-start">
                <div class="w-100 pv7 relative" :class="{ 'di-step-4': wildcardsLength > 0 }">
                  <v-hover v-slot="{ hover }">
                    <v-btn block height="64" 
                      :elevation="hover ? 8 : 0"
                      :outlined="(currentStep > 2) ? false : true"
                      :color="steps[3].color"
                      :class="{'white--text': currentStep > 2 }"
                      :disabled="currentStep < 3"
                      @click="addFactor(4)"
                      class="rounded-lg"
                    >
                      {{ steps[3].title }}
                    </v-btn>
                  </v-hover>
                  <v-btn
                    fab
                    rounded
                    text
                    elevation="0"
                    class="indicator-4"
                  >4</v-btn>
                  <div class="triangle-up"></div>
                </div>
              </div>

              <div class="w-2"></div>
              
              <div class="w-third flex flex-column justify-center di-r">
                <div class="chip-wrap" v-for="(item, i) in wildcards" :key="i" :class="[nodeClass(i, wildcardsLength)]">
                  <v-chip
                    label
                    link
                    :color="steps[3].color"
                    text-color="white"
                    class="rounded-lg"
                  >{{ item.title }}</v-chip>
                </div>
              </div>

            </div>
          </div>

        </div>

      </div>
    </section>

    <!--edit factor-->
    <v-dialog
      v-model="dialog"
      persistent
      max-width="360"
    >
      <v-card :color="(editedFactor.type) ? steps[editedFactor.type - 1].color : null">
        <v-card-title class="headline">
          
        </v-card-title>

        <v-card-text>
          <v-textarea
            v-model="editedFactor.title"
            name="input-7-1"
            :label="(editedFactor.label) ? editedFactor.label : null"
            rows="3"
            hide-details
            dark
          ></v-textarea>
        </v-card-text>

        <v-card-actions>
          <v-menu
            v-if="editedFactor.id"
            transition="slide-y-transition"
            bottom
            rounded="lg"
            class=""
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="white"
                text
                v-bind="attrs"
                v-on="on"
              >
                Usuń
              </v-btn>
            </template>
            <v-list dark color="red">
              <v-list-item @click="delFactor">
                <v-list-item-title>Potwierdzam</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
          <v-spacer></v-spacer>

          <v-btn
            color="white"
            text
            @click="dialog = false"
          >
            Anuluj
          </v-btn>

          <v-btn
            color="white"
            text
            @click="insertFactor"
          >
            Zapisz
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

  </div>
</template>

<script>
// @ is an alias to /src
//import HelloWorld from '@/components/HelloWorld.vue'
import { mapGetters, mapActions, mapMutations } from 'vuex'

export default {
    data: () => ({
      dialog: false,
      dialogKey: false,
      keyFactorsSelection: [],
      dialogm1: '',
      editedFactor: {
        id: '',
        id_topic: '',
        title: '',
        ord: '',
        type: ''
      },
    }),
    computed: {
      ...mapGetters('scenarios', ['id','currentStep', 'factorsByType','factors', 'factorsLength', 'keyFactors', 'forces','forcesLength','wildcards','wildcardsLength','keyFactorsLength', 'title', 'steps', 'loading']),
      keyFactorsSelectionLength() {
        return this.keyFactorsSelection.length
      },
    },
    watch: {
      dialogKey(opened) {
        if (opened) {
          this.keyFactorsSelection = this.keyFactors.map(el => el.id);
        }
      }
    },
    methods: {
      ...mapActions('scenarios', ['getTopic','updateFactor','updateKeyFactors','deleteFactor']),
      ...mapMutations('scenarios', ['setStepSlide']),
      nodeClass(i, all) {
        if (i == 0 && all == 1) {
          return { 'di-single': true }
        } else if (i == 0) {
          return { 'di-first': true }
        } else if (i == all - 1) {
          return { 'di-last': true }
        } else {
          return { 'di-mid': true }
        }
        
      },
      addFactor(type) {
        var labels = {
          '1': 'Dodaj czynnik',
          '3': 'Dodaj siłę napędową',
          '4': 'Dodaj wild card'
        };
        
        this.editedFactor = {
          id: '',
          id_topic: this.id,
          title: '',
          type: type,
          key_factor: '0',
          ord: '0',
          label: labels[type]
        }
        this.dialog = true;
      },
      editFactor(factor) {
        var label = 'Czynnik';
        this.editedFactor = {...factor};
        this.editedFactor.label = label;
        this.dialog = true;
      },
      insertFactor() {
        this.updateFactor(this.editedFactor)
        .then(() => this.dialog = false );
      },
      delFactor() {
        this.deleteFactor(this.editedFactor)
        .then(() => this.dialog = false );
      },
      saveKeyFactors() {
        //keyFactorsSelection
        //let selectedIds = this.keyFactorsSelection.map(el => el.id);
        
        this.updateKeyFactors({
          id_topic: this.id,
          keyFactorsId: this.keyFactorsSelection //.map(el => el.id)
        });
        
        this.dialogKey = false;
      }
    }
  }
</script>

<style>
.chip-wrap .v-chip--clickable {
  will-change: box-shadow;
  transition-duration: 0.28s;
  transition-property: box-shadow, transform, opacity;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.chip-wrap .v-chip--clickable:hover {
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12) !important;
}
.bgbaner {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}
/*radial waves*/
.css-xuabex {
    position: absolute;
    left: 50%;
    top: 50%;
    /*position: relative;
    width: 5.5rem;
    height: 5.5rem;
    margin: 0 auto;
    background-repeat: no-repeat;
    background-position: 50%;*/
    
}
.css-xuabex > div:nth-of-type(2) {
    -webkit-animation-delay: 0.2s;
    animation-delay: 0.2s;
}
.css-xuabex > div:nth-of-type(3) {
    -webkit-animation-delay: 0.5s;
    animation-delay: 0.5s;
}
.css-1kyirur {
    position: absolute;
    top: 50%;
    left: 50%;
    border-radius: 50%;
    border: 10px solid gray;
    width: 100px;
    height: 100px;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    -webkit-animation: animation-1nnaaoa 2s infinite;
    animation: animation-1nnaaoa 2s infinite;
}
@-webkit-keyframes animation-1nnaaoa {
    0% {
        width: 100px;
        height: 100px;
        border: 40px solid gray;
        opacity: 0;
    }
    50% {
        opacity: 0.1;
    }
    90% {
        width: 500px;
        height: 500px;
        border: 2px solid gray;
        opacity: 0;
    }
    100% {
        width: 1000px;
        height: 1000px;
        border: 2px solid gray;
        opacity: 0;
    }
}
@keyframes animation-1nnaaoa {
    0% {
        width: 100px;
        height: 100px;
        border: 2px solid gray;
        opacity: 0;
    }
    50% {
        opacity: 0.1;
    }
    90% {
        width: 500px;
        height: 500px;
        border: 2px solid gray;
        opacity: 0;
    }
    100% {
        width: 1000px;
        height: 1000px;
        border: 2px solid gray;
        opacity: 0;
    }
}
